	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: 'GPZ_video_tape(normal)';
	src: url('../font/GPZ_video_tape_normal.otf');
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: 'SEBANG_Gothic_Bold';
